<template>
  <div class="ComCecord">
    <div class="flex recordTitle">
      <p @click="goback">
        <i style="font-size:16px;" class="el-icon-arrow-left"></i>
      </p>
      <p style="font-weight: 600;">佣金记录</p>
      <p></p>
    </div>
    <!--  -->
    <div style="display:flex;justify-content: flex-end; align-items: center;margin:10px">
      <el-date-picker @change="getstartTime" value-format="yyyy-MM-dd HH:mm:ss" style="width:40%" size="mini" v-model="startTime" type="date" placeholder="开始时间"></el-date-picker>
      <span>-</span>
      <el-date-picker @change="getendTime" value-format="yyyy-MM-dd HH:mm:ss" style="width:40%" size="mini" v-model="endTime" type="date" placeholder="结束时间"></el-date-picker>
    </div>
    <!-- content -->
    <div class="recordContent">
      <ul>
        <li v-for="(item,index) in recordList" :key="index" style="margin-top:10px" class="contentli">
          <div class="flex ul_T">
            <p>{{item.saveDay}}</p>
            <p>+{{item.commissionAmount}}</p>
          </div>
          <div class="flex ul_txt">
            <ul>
              <li>
                <p>交易笔数</p>
                <p>{{item.orderNum }}</p>
              </li>
              <li>
                <p>退款笔数</p>
                <p>{{item.refundNum}}</p>
              </li>
              <li>
                <p>销售金额</p>￥
                <p class="lanse">{{item.validDealAmount}}</p>
              </li>
            </ul>
            <ul>
              <li>
                <p>交易总额</p>￥
                <p class="lanse">{{item.orderAmount}}</p>
              </li>
              <li>
                <p>退款金额</p>￥
                <p>{{item.refundAmount}}</p>
              </li>
              <li>
                <p>佣金总额</p>￥
                <p>{{item.commissionAmount}}</p>
              </li>
            </ul>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { homeApi } from "@/api";
import * as types from "@/router/types";
import { afterLoginInfoLocal } from "@/storage";
export default {
  data() {
    return {
      value1: "",
      type: 1,
      companyId: "",
      startTime: "",
      endTime: "",
      recordList:[]
    };
  },
  created() {
    this.startTime = this.getDay(-30);
    this.endTime = this.getDay(0);
    let userInfo = afterLoginInfoLocal.getJSON();
    this.companyId = userInfo.companyId;
    console.log(this.companyId)
    this.getCommissionRecord();
  },
  methods: {
    goback() {
      this.$router.back(-1);
    },
    getstartTime(val) {
      console.log(val, "val");
      this.startTime = val;
      this.getCommissionRecord();
    },
    getendTime(val) {
      console.log(val, "val");
      this.endTime = val;
      this.getCommissionRecord();
    },
    // 获取日期
    getDay(day) {
      var today = new Date();
      var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      var tHours = today.getHours();
      var tMinutes = today.getMinutes();
      var tSeconds = today.getSeconds();
      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return (
        tYear +
        "-" +
        tMonth +
        "-" +
        tDate +
        " " +
        tHours +
        ":" +
        tMinutes +
        ":" +
        tSeconds
      );
    },
    doHandleMonth(month) {
      var m = month;
      if (month.toString().length == 1) {
        m = "0" + month;
      }
      return m;
    },
    //获取佣金记录
    getCommissionRecord() {
      homeApi
        .CommissionRecord(
          this.companyId,
          this.startTime,
          this.endTime,
          this.payPriceType
        )
        .then(
          res => {
            this.recordList = res.obj;
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    }
  }
};
</script>
<style scoped>
.ul_txt {
  padding: 10px 20px;
}
.contentli {
  margin-top: 10px;
}
.ul_txt ul li {
  display: flex;
}
.ul_txt ul li p:first-of-type {
  margin-right: 10px;
  color: #8c8c8c;
}
.ul_txt ul {
  width: 50%;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.lanse {
  color: #0066cc;
  font-size: 12px;
  font-weight: inherit;
}
.ul_T p:first-of-type {
  font-weight: 600;
}
.ul_T p:last-of-type {
  color: #ff7373;
  font-weight: bold;
}
.recordContent ul li {
  border-radius: 6px;
  background: #fff;
  width: 95%;
  margin: auto;
}
.ul_T {
  border-bottom: 1px dashed #e3e3e3;
  padding: 5px 10px;
}
.ComCecord {
  background: #eeeeee;
}
.recordTitle {
  padding: 10px;
  background: #fff;
  height: 40px;
}
</style>


